import * as React from 'react';
import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';
import { Grid, TableCell, TableRow } from '@material-ui/core';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { ProcedureData, DiagnosisData, LOAData } from 'Pages/CustomerCarePage/Models';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { DatePicker } from 'Pages/CustomerCarePage/Components/DatePicker';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faPlus, faPen, faMinusCircle, faCheckCircle, faTimesCircle } from '@fortawesome/free-solid-svg-icons';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { ConfinementDetails } from '../ResolveTicketPage';

interface IProps {
    confinement: ConfinementDetails;
    loas: LOAData[];
    refreshLOAStates: () => void;
}

interface IStates {
    loaNumber: number;
    amount: number;
    loaDate: Date;
    notes: string;

    indexToEdit: number;
    editLoaNumber: number;
    editAmount: number;
    editLoaDate: Date;
    editNotes: string;
}

export class SummaryInpatientSection extends React.Component<IProps, IStates> {

    APPROVED = 'Approved';
    DISAPPROVED = 'Disapproved';
    PENDING = 'Pending';

    constructor(props: IProps) {
        super(props);

        this.state = {
            loaNumber: 0,
            amount: 0,
            loaDate: new Date(),
            notes: '',

            indexToEdit: -1,
            editLoaNumber: 0,
            editAmount: 0,
            editLoaDate: new Date(),
            editNotes: ''
        }
    }

    getProceduresCost = (diagnosis: DiagnosisData) => {
        let total = 0;
        diagnosis.procedures.map(procedure => {
            total += procedure.amount;
        })
        return total;
    }

    getApprovedAvailments = () => {
        const approvedAvailments = this.props.confinement.diagnoses.map(diagnosis => {
            return {
                ...diagnosis,
                procedures: diagnosis.procedures
                    .filter(procedure => procedure.assessmentType === this.APPROVED)
                    .map(procedure => {
                        return procedure;
                    })
            };
        });
        return approvedAvailments.filter(availment => availment.procedures.length > 0);
    }

    getDisapprovedAvailments = () => {
        const disapprovedAvailments = this.props.confinement.diagnoses.map(diagnosis => {
            return {
                ...diagnosis,
                procedures: diagnosis.procedures
                    .filter(procedure => procedure.assessmentType === this.DISAPPROVED)
                    .map(procedure => {
                        return procedure;
                    })
            };
        });
        return disapprovedAvailments.filter(availment => availment.procedures.length > 0);
    }

    getApprovedAvailmentsCost = () => {
        let total = 0;
        this.props.confinement.diagnoses.map(diagnosis => {
            diagnosis.procedures.map(procedure => {
                total += (procedure.assessmentType === this.APPROVED ? procedure.amount : 0);
            });
        });
        return total;
    }

    render(): JSX.Element {
        const approvedAvailments = this.getApprovedAvailments();
        const disapprovedAvailments = this.getDisapprovedAvailments();
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Summary of Approved Availments</div>
                <div className='sub-section'>
                    <div className='light-text'>PROVIDER</div>
                    <div className='small-text'>
                    {this.props.confinement.provider.label}</div>
                    <div style={{float: 'right'}}>
                        <span className='small-text'>TOTAL AMOUNT</span>
                        <span style={{marginLeft: '1rem'}} />
                        <span className='header-2'>
                            {'PHP ' + GlobalFunction.formatNumber(this.getApprovedAvailmentsCost(), true)}
                        </span>
                    </div>
                </div>
                {approvedAvailments.length > 0 ? (
                    <div className='sub-section'>
                        <ComplexTable 
                            data={this.getApprovedAvailments()}
                            renderHeader={() => this.renderHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderRow(rowData, index)} />
                    </div>
                ) : null}
                <div className='sub-section'>
                    {/* <ComplexTable 
                        data={this.props.loas}
                        renderHeader={() => this.renderLOAHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderLOARow(rowData, index)} /> */}
                    <CustomTable
                        rows={this.props.loas}
                        renderHeader={() => this.renderLOAHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderLOARow(rowData, index)} />
                </div>
                <div className='sub-section' />
                {disapprovedAvailments.length > 0 ? (
                    <>
                    <div className='header-1' style={{color: '#FD5451'}}>Disapproved Availments</div>
                    <div className='sub-section'>
                        <ComplexTable 
                            data={this.getDisapprovedAvailments()}
                            renderHeader={() => this.renderHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderRow(rowData, index)} />
                    </div>
                    </>
                ) : null}
                <div className='section' />
            </div>
        );
    }

    renderHeader(): JSX.Element {
        const headerNameStyle = {
            margin: 0,
            padding: '12px 0 12px 0'
        };

        return (
            <>
                <Grid item xs={4}>
                    <div className='header-2' style={headerNameStyle}>Diagnosis</div>
                </Grid>
                <Grid item xs={4}>
                    <div className='header-2' style={headerNameStyle}>Doctor</div>
                </Grid>
                <Grid item xs={2}>
                    <div className='header-2' style={headerNameStyle}>Professional Fee</div>
                </Grid>
                <Grid item xs={2}>
                    <div className='header-2' style={{...headerNameStyle, textAlign: 'right'}}>
                        Total Amt. of Procedures
                    </div>
                </Grid>
            </>
        );
    }

    renderRow(diagnosis: DiagnosisData, _index: number): JSX.Element {
        return (
            <>
                <Grid className='small-section' container>
                    <Grid item xs={4}>
                        {diagnosis.icd10Code.value + ' - ' + diagnosis.icd10Code.label}
                    </Grid>
                    <Grid item xs={4}>
                        {diagnosis.doctors[0].id + ' - ' + diagnosis.doctors[0].name}
                    </Grid>
                    <Grid item xs={2}>
                        {'PHP ' + GlobalFunction.formatNumber(diagnosis.doctors[0].fee, true)}
                    </Grid>
                    <Grid item xs={2} style={{fontWeight: 'bold', textAlign: 'right'}}>
                        {'PHP ' + GlobalFunction.formatNumber(this.getProceduresCost(diagnosis), true)}
                    </Grid>
                </Grid>
                <div className='line-border' />
                {diagnosis.procedures.map((procedure, index) => {
                    return this.renderProcedure(procedure, index);
                })}
            </>
        );
    }

    renderProcedure(procedure: ProcedureData, index: number): JSX.Element {

        return (
            <>
            <div className='line-border' />
            <div className='section' >
                <Grid container>
                    <Grid item xs={4}>
                        <div>
                            <Grid container>
                                <Grid item xs={2} style={{padding: '0 0.5rem', marginTop: '-3px'}}>
                                    <div className='number-item'>
                                    {index + 1}
                                    </div>
                                </Grid>
                                <Grid item xs={10} className='header-2'>
                                    {procedure.cptCode.value + ' - ' + procedure.cptCode.label}
                                </Grid>
                            </Grid>
                        </div>
                    </Grid>
                </Grid>
                <Grid container>
                    <Grid item xs={4}>
                        <div>
                            <Grid container>
                                <Grid item xs={2} />
                                <Grid item xs={10}>
                                    <div className='sub-section'>
                                        <div className='light-text'>AMOUNT</div>
                                        <div className='small-text'>{'PHP ' + GlobalFunction.formatNumber(procedure.amount, true)}</div>
                                    </div>
                                    <div className='sub-section'>
                                        <div className='light-text'>PHILHEALTH REQUIRED?</div>
                                        <div className='small-text'>{procedure.isPHIC ? 'Yes' : 'No'}</div>
                                    </div>
                                    <div className='sub-section'>
                                        <div className='light-text'>DATE OF AVAILMENT</div>
                                        <div className='small-text'>{'May 19, 2019 *'}</div>
                                    </div>
                                </Grid>
                            </Grid>
                        </div>
                    </Grid>
                    <Grid item xs={4}>
                        <div className='sub-section'>
                            <div className='light-text'>SYSTEM ASSESSMENT</div>
                            <div className='small-text'>{'Approve *'}</div>
                        </div>
                        <div className='sub-section'>
                            <div className='light-text'>REMAINING BALANCE</div>
                            <div className='small-text'>{'PHP ' + 0 + '.00 *'}</div>
                        </div>
                        <div className='sub-section'>
                            <div className='light-text'>CLAUSE</div>
                            <div className='small-text'>{'Laboratory tests, x-rays, and other diagnostic examinations prescribed by the INTELLICARE physician*'}</div>
                        </div>
                    </Grid>
                    <Grid item xs={2}>
                        <div className='sub-section'>
                            <div className='light-text'>ASSESSMENT</div>
                            <div className='header-2'>{procedure.assessmentType}</div>
                        </div>
                        <div className='sub-section'>
                            <div className='light-text'>REMARKS</div>
                            <div className='small-text'>
                                {procedure.assessmentRemarks ? procedure.assessmentRemarks : 'NONE'}
                            </div>
                        </div>
                    </Grid>
                </Grid>
            </div>
            </>
        );
    }

    renderLOAHeader(): JSX.Element {
        return (
            <>
            <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>
                <div className='header-2'>LOA Number</div>
                <NumberField
                    value={this.state.loaNumber}
                    onChange={(value: number) => this.setState({loaNumber: value})} />
            </TableCell>
            <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>
                <div className='header-2'>Amount</div>
                <NumberField
                    value={this.state.amount}
                    onChange={(value: number) => this.setState({amount: value})} />
            </TableCell>
            <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>
                <div className='header-2'>Date</div>
                <DatePicker
                    value={this.state.loaDate}
                    onChange={(value: Date) => this.setState({loaDate: value})} />
            </TableCell>
            <TableCell>
                <div className='header-2'>Notes</div>
                <InputField
                    value={this.state.notes}
                    onChange={(value: string) => this.setState({notes: value})} />
            </TableCell>
            <TableCell style={{minWidth: '75px', maxWidth: '75px', textAlign: 'center'}}>
                <TextButton 
                    label='Add LOA'
                    icon={faPlus}
                    onClick={() => {
                        this.props.loas.push({
                            loaNo: this.state.loaNumber,
                            amount: this.state.amount,
                            date: this.state.loaDate,
                            notes: this.state.notes
                        });
                        this.props.refreshLOAStates();
                        this.setState({
                            loaNumber: 0, 
                            amount: 0,
                            loaDate: new Date(),
                            notes: ''
                        });
                    }}/>
            </TableCell>
            </>
        );
    }

    renderLOARow(loa: LOAData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                {this.state.indexToEdit !== index ? (
                    <>
                    <TableCell>{loa.loaNo}</TableCell>
                    <TableCell>{GlobalFunction.formatNumber(loa.amount)}</TableCell>
                    <TableCell>{GlobalFunction.toCommonDateString(loa.date)}</TableCell>
                    <TableCell>{loa.notes}</TableCell>
                    <TableCell>
                        <Grid container>
                            <Grid item xs={6} />
                            <Grid item xs={3}>
                                <IconButton
                                    icon={faPen}
                                    onClick={() => {
                                        this.setState({indexToEdit: index});
                                    }}  />
                            </Grid>
                            <Grid item xs={3}>
                                <IconButton
                                    icon={faMinusCircle}
                                    onClick={() => {
                                        this.props.loas.splice(index, 1);
                                        this.props.refreshLOAStates();
                                    }}/>
                            </Grid>
                        </Grid>
                    </TableCell>
                    </>
                ) : (
                    <>
                    <TableCell>
                        <NumberField
                            value={this.state.editLoaNumber}
                            onChange={(value: number) => this.setState({editLoaNumber: value})} />
                    </TableCell>
                    <TableCell>
                        <NumberField
                            value={this.state.editAmount}
                            onChange={(value: number) => this.setState({editAmount: value})} />
                    </TableCell>
                    <TableCell>
                        <DatePicker
                            value={this.state.editLoaDate}
                            onChange={(value: Date) => this.setState({editLoaDate: value})} />
                    </TableCell>
                    <TableCell>
                        <InputField
                            value={this.state.editNotes}
                            onChange={(value: string) => this.setState({editNotes: value})} />
                    </TableCell>
                    <TableCell>
                        <Grid container>
                            <Grid item xs={6} />
                            <Grid item xs={3}>
                                <IconButton
                                    icon={faCheckCircle}
                                    onClick={() => {
                                        this.props.loas[index] = {
                                            loaNo: this.state.editLoaNumber,
                                            amount: this.state.editAmount,
                                            date: this.state.editLoaDate,
                                            notes: this.state.editNotes
                                        };
                                        this.setState({indexToEdit: -1});
                                    }}  />
                            </Grid>
                            <Grid item xs={3}>
                                <IconButton
                                    icon={faTimesCircle}
                                    onClick={() => {
                                        this.setState({indexToEdit: -1});
                                    }}/>
                            </Grid>
                        </Grid>
                    </TableCell>
                    </>
                )}
                
            </TableRow>
        );
    }
}