import * as React from 'react';
import {  ConsultationDetails } from 'Pages/CustomerCarePage/Models';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';
import { Grid } from '@material-ui/core';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';

interface IProps {
    consultation: ConsultationDetails;
    refreshConsultationState: () => void;
}

interface IStates {
    selectedTab: any;
}

export class ApproveConsultationSection extends React.Component<IProps, IStates> {

    APPROVED = 'Approved';
    DISAPPROVED = 'Disapproved';
    PENDING = 'Pending';

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 0
        }
    }

    render(): JSX.Element {
        console.log(this.props.consultation)
        const consultation = this.props.consultation;
        return (
            <Grid container className='section'>
                <Grid item xs={6}>
                    <div style={{paddingTop: '2.5rem'}} />
                    <div className='header-1'>Resolve Consultation</div>
                    <div className='sub-section-2'>
                        <div className='light-text'>PROVIDER</div>
                        <div className='small-text'>
                            {`${consultation.provider.value} - ${consultation.provider.label}`}
                        </div>
                    </div>
                    <div className='sub-section-2'>
                        <div className='light-text'>DIAGNOSIS</div>
                        <div className='small-text'>
                            {`${consultation.diagnosis.value} - ${consultation.diagnosis.label}`}
                        </div>
                    </div>
                    <div className='sub-section-2'>
                        <div className='light-text'>DOCTOR</div>
                        <div className='small-text'>
                            {`${consultation.doctor.value} - ${consultation.doctor.label}`}
                        </div>
                    </div>
                    <div className='sub-section-2'>
                        <div className='light-text'>NOTES</div>
                        <div className='small-text'>{consultation.notes ? consultation.notes : 'None'}</div>
                    </div>
                    <div className='sub-section'>
                    <div className='header-2'>ASSESSMENT</div>
                        <CustomRadio 
                            label='Approve'
                            checkedCondition={consultation.assessmentType === this.APPROVED}
                            onChange={() => {
                                consultation.assessmentType = this.APPROVED;
                                this.props.refreshConsultationState();
                            }} />
                        <CustomRadio 
                            label='Disapprove'
                            checkedCondition={consultation.assessmentType === this.DISAPPROVED}
                            onChange={() => {
                                consultation.assessmentType = this.DISAPPROVED;
                                this.props.refreshConsultationState();
                            }} />
                        <CustomRadio 
                            label='Pending'
                            checkedCondition={consultation.assessmentType === this.PENDING}
                            onChange={() => {
                                consultation.assessmentType = this.PENDING;
                                this.props.refreshConsultationState();
                            }} />
                        <InputField
                            placeholder='Reason'
                            value={consultation.assessmentRemarks}
                            onChange={(value: string) => {
                                consultation.assessmentRemarks = value;
                                this.props.refreshConsultationState();
                            }} />
                    </div>
                    <div className='section' />
                </Grid>
            </Grid>
        );
    }
}