import * as React from 'react';
import { Button } from '@material-ui/core';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';

interface IProps {
    isUpdating: boolean;
    onUpdateTicket: () => void;
    onSaveForNow: () => void;
    onAssign: () => void;
    onEscalate: () => void;
    onResolve: () => void;
}

export class UpdateTicketFooter extends React.Component<IProps> {
    
    constructor(props: IProps) {
        super(props);
    }

    shouldComponentUpdate(_props: IProps, _state: any) {
        return false;
    }

    render(): JSX.Element {
        return (
            <div className='footer'>
                <Button className='button-transparent'>
                        Save For Now
                </Button>
                <Button 
                    className='button-neutral'
                    onClick={this.props.onAssign}>
                    Assign
                </Button>
                <Button 
                    className='button-error'
                    onClick={this.props.onEscalate}>
                    Escalate
                </Button>
                <Button 
                    className='button'
                    style={{minWidth: '150px'}}
                    onClick={this.props.onUpdateTicket}>
                    {this.props.isUpdating ? (
                        <Loading color='#FFFFFF' size={32} />
                    ) : ('Update Ticket')}
                    
                </Button>
                <Button 
                    className='button'
                    style={{minWidth: '150px'}}
                    onClick={this.props.onResolve}>
                    {this.props.isUpdating ? (
                        <Loading color='#FFFFFF' size={32} />
                    ) : ('Proceed with Final Assessment')}
                </Button>
                
            </div>
        );
    }
}