import * as React from 'react';
import { Button } from '@material-ui/core';

interface IProps {
    isSummarizing: boolean;
    onAssign: () => void;
    onEscalate: () => void;
    onSummarize: () => void;
    onResolve: () => void;
}

export class ResolveTicketFooter extends React.Component<IProps> {
    
    constructor(props: IProps) {
        super(props);
    }

    render(): JSX.Element {
        return (
            <div className='footer'>
                <Button 
                    className='button-neutral'
                    onClick={this.props.onAssign}>
                    Assign
                </Button>
                <Button 
                    className='button-error'
                    onClick={this.props.onEscalate}>
                    Escalate
                </Button>
                {this.props.isSummarizing ? (
                    <Button 
                        className='button'
                        onClick={this.props.onResolve}>
                        Mark as Resolved
                    </Button>
                ) : (
                    <Button 
                        className='button'
                        onClick={this.props.onSummarize}>
                        Summarize Assessment
                    </Button>
                )}
                
                
            </div>
        );
    }
}