import * as React from 'react';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { Grid, TableCell } from '@material-ui/core';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';

import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { DatePicker } from 'Pages/CustomerCarePage/Components/DatePicker';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { UpdateData } from '../UpdateTicketProps';
// import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
// import { CRUDTable } from 'Pages/CustomerCarePage/UpdateTicketPage/Components/CRUDTable';

import { CRUDTable } from './CRUDTable';

interface IProps {
    updates: UpdateData[];
    refreshUpdatesState: () => void;
}

interface IStates {
    remarks: string;
    date: Date;
    hospitalBill: number;
}

export class UpdateSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            remarks: '',
            date: new Date(),
            hospitalBill: 0
        }
    }

    onFormatTableHeader = (colIndex: number, value: any) => {
        return (
            colIndex === 2 ? (
                <TableCell style={{textAlign: 'right', minWidth: '200px'}}>{value}</TableCell>
            ) : (
                <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
            )
        );
    }

    onFormatCRUDTable = (colIndex: number, value: any) => {
        return (
            colIndex === 1 ? (
                <div>{GlobalFunction.toCommonDateString(value)}</div>
            ) : colIndex === 2 ? (
                <div style={{textAlign: 'right'}}>
                    {GlobalFunction.formatNumber(value, true) + ' PHP'}
                </div>
            ) : (value)
        );
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Add Update</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <InputField 
                                placeholder='Remarks'
                                value={this.state.remarks}
                                charLimit={100}
                                onChange={(value: string) => {
                                    this.setState({remarks: value});
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            <DatePicker 
                                placeholder='As of Date'
                                value={this.state.date}
                                onChange={(value: Date) => {
                                    this.setState({date: value});
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            <DetailedNumberField 
                                placeholder='Running Hospital Bill'
                                detail='PHP'
                                isFloat={true}
                                value={this.state.hospitalBill}
                                onChange={(value: number) => {
                                    this.setState({hospitalBill: value});
                                }}/>
                        </Grid>
                    </Grid>
                    <div style={{paddingTop: '5px'}} />
                    <TextButton 
                        label='Add update' 
                        icon={faPlus}
                        onClick={() => {
                            var state = this.state;
                            const tempData = {
                                remarks: state.remarks,
                                date: state.date,
                                hospitalBill: state.hospitalBill
                            }
                            this.props.updates.unshift(tempData);
                            this.props.refreshUpdatesState();
                            this.setState({
                                remarks: '',
                                date: new Date(),
                                hospitalBill: 0
                            });
                        }}/>
                </div>
                
                <div className='sub-section'>
                    {/* <CustomTable
                        className='custom-table'
                        rows={this.props.updates}
                        renderHeader={() => {
                            return (
                                <>
                                <TableCell>Remarks</TableCell>
                                <TableCell>As of Date</TableCell>
                                <TableCell>Running hospital bill</TableCell>
                                <TableCell style={{width: '100px'}}/>
                                </>
                            );
                        }}
                        renderRow={(update: UpdateData, index: number) => {
                            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                            return (
                                <TableRow style={{backgroundColor: backgroundColor}}>
                                    <TableCell>{update.remarks}</TableCell>
                                    <TableCell>{GlobalFunction.toCommonDateString(update.date)}</TableCell>
                                    <TableCell>{GlobalFunction.formatNumber(update.hospitalBill) + '.00 PHP'}</TableCell>
                                    <TableCell>
                                        <div style={{display: 'inline-block', paddingRight: '0.5rem'}}>
                                            <IconButton icon={faPen} onClick={() => {
                                                //this.setState({rowToEdit: index1});
                                                //this.setState({data: row});
                                            }} />
                                        </div>
                                        <div style={{display: 'inline-block'}}>
                                            <IconButton icon={faMinusCircle} onClick={() => {
                                                //this.props.onDeleteRow(index1)
                                                this.props.updates.splice(index, 1);
                                                this.props.refreshUpdatesState();
                                            }} />
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ); 
                        }} /> */}
                    <CRUDTable 
                        className='custom-table'
                        placeholder='UPDATES'
                        
                        cols={['Remarks', 'As of Date', 'Running Hospital Bill']}
                        rows={this.props.updates} 
                        onSaveEditRow={(index: number, data: any) => {
                            this.props.updates[index] = data;
                            this.props.refreshUpdatesState();
                        }}
                        onDeleteRow={(index: number) => {
                            this.props.updates.splice(index, 1);
                            this.props.refreshUpdatesState();
                        }}
                        onHeaderFormat={(colIndex: number, colName: string) => {
                            return this.onFormatTableHeader(colIndex, colName);
                        }} 
                        onFormat={(_rowIndex: number, colIndex: number, value: any) => {
                            return this.onFormatCRUDTable(colIndex, value);
                        }} 
                        editField={(colIndex: number, currentVal: any, onChange: any) => {
                            if (colIndex === 0) {
                                return (
                                    <InputField
                                        value={currentVal}
                                        onChange={(value: string) => onChange(value)}/>
                                );
                            } else if (colIndex === 1) {
                                return (
                                    <DatePicker
                                        value={currentVal}
                                        onChange={(value: Date) => onChange(value)}/>
                                );
                            } else {
                                return (
                                    <DetailedNumberField
                                        detail='PHP'
                                        value={currentVal}
                                        onChange={(value: number) => onChange(value)}/>
                                );
                            }
                        }} />
                </div>
                <div className='section' />
            </div>
        );
    }
}