
let backendUrl = process.env.REACT_APP_BASE_URL;

export class API {

    // //local
    // static localUrl = 'http://18.138.94.224:4101/api';//'https://neo-local.seerlabs.com/api';

    // //stg
    // static stgUrl = 'http://18.138.94.224:4101/api';

    // static getUrl() {
    //     return (process.env.NODE_ENV === 'development' ? this.localUrl : this.stgUrl);
    // }

    static getUrl() {
        return process.env.REACT_APP_BASE_URL;
    }

    static async get(url: string, success: Function, fail: Function) {
        // const backendUrl = (process.env.NODE_ENV === 'development' ? this.localUrl : this.stgUrl);
        fetch(backendUrl + url, {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' }
        })
        .then(response => response.json())
        .then(data => success(data))
        .catch(error => fail(error));
    }

    static async post(url: string, 
                      body: any,
                      success: Function, 
                      fail: Function,
                      customHeader: any = { 'Content-Type': 'application/json' }) {
        // const backendUrl = (process.env.NODE_ENV === 'development' ? this.localUrl : this.stgUrl);
        fetch(backendUrl + url, {
            method: 'POST',
            headers: customHeader,
            body: body
        })
        .then(response => response.json())
        .then(data => success(data))
        .catch(error => fail(error));
    }

    static async put(url: string, body: any, success: Function, fail: Function) {
        // const backendUrl = (process.env.NODE_ENV === 'development' ? this.localUrl : this.stgUrl);
        fetch(backendUrl + url, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
        .then(response => response)
        .then(data => success(data))
        .catch(error => fail(error));
    }
}