import * as React from 'react';
import { Button } from '@material-ui/core';

interface IProps {
    showResolveButton: boolean;
    onSubmitTicket: () => void;
    onResolveTicket: () => void;
    onSaveForNow: () => void;
}

export class SaveTicketFooter extends React.Component<IProps> {

    shouldComponentUpdate(props: IProps, _state: any) {
        return (props.showResolveButton != this.props.showResolveButton);
    }

    render(): JSX.Element {
        return (
            <div className='footer'>
                <Button className='button-transparent'>
                    Save For Now
                </Button>
                {this.props.showResolveButton ? (
                    <Button 
                        className='button'
                        onClick={this.props.onResolveTicket}>
                        Mark as Resolved
                    </Button>
                ) : null}
                
                <Button 
                    className='button'
                    onClick={this.props.onSubmitTicket}>
                    Submit Ticket
                </Button>
                
            </div>
        );
    }
}