import * as React from 'react';
import { match } from "react-router-dom";
import {
    UpdateData, 
    AccommodationData, 
    DiagnosisData, 
    PhysicianData, 
    ProcedureData,
    ItemData,
    FileData
} from './UpdateTicketProps';
import { Grid, Button } from '@material-ui/core';
import { SummarySection } from './Components/SummarySection';
import { UpdateSection } from './Components/UpdateSection';
import { API } from '../API';
import { AccommodationsSection } from './Components/AccommodationsSection';
import { DiagnosesSection } from './Components/DiagnosesSection';
import { PhysiciansSection } from './Components/PhysiciansSection';
import { ProceduresSection } from './Components/ProceduresSection';
import { OtherDetailsSection } from './Components/OtherDetailsSection';
import { DischargeDetailsSection } from './Components/DischargeDetailsSection';
import { UploadedDocumentsSection } from './Components/UploadedDocumentsSection';
import { MemberDataSection } from '../MemberDataSection';
import { DropdownChoice, TicketData, MemberData, InquiryData } from '../Models';
import { UpdateTicketFooter } from './Components/UpdateTicketFooter';
import { UpdateInquirySection } from './Components/UpdateInquirySection';
import { CustomTabs } from '../Components/CustomTab';
import { Payloads } from '../Payloads';
import { Processmaker } from '../Processmaker';
import { Loading } from '../Components/Loading';
import { CustomModal } from '../Components/CustomModal';

interface IProps {
    required: string;
    match: match<MatchParams>;
    history: any;
}

interface MatchParams {
    id: string;
}

interface IStates {
    tabSelected: number;
    loadedResources: number;
    isAssessmentModalOpen: boolean;

    member?: MemberData;
    ticket: TicketData;
    
    /* Inpatient states */
    confinement?: ConfinementDetails;

    /* Inquiry States */
    inquiries?: InquiryData[];
}

export interface ConfinementDetails {
    provider: DropdownChoice;
    dateAdmitted: Date;
    confinementKind: string;
    giftGiven: boolean;
    coverage: DropdownChoice;
    philhealthAmount: number;
    finalBill: number;
    dischargeDate: Date | null;
    primaryDiagnosisIndex: number;

    updates: UpdateData[];
    accommodations: AccommodationData[];
    diagnoses: DiagnosisData[];
    physicians: PhysicianData[];
    procedures: ProcedureData[];
    items: ItemData[];
    files: FileData[];
}

interface ErrorMessageFormat {
    message: string;
    targets: string[];
}

export class UpdateTicketPage extends React.Component<IProps, IStates> {

    static RESOURCES_TO_LOAD = 5;

    tabList: { value: number, label: string }[];
    icd10Codes: DropdownChoice[];
    cptCodes: DropdownChoice[];
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    agents: DropdownChoice[];

    errors: ErrorMessageFormat[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            tabSelected: 1,
            loadedResources: 0,
            isAssessmentModalOpen: false,

            ticket: {
                ticketChannel: '',
                transactionNo: '',
                ticketSource: '',
                memberName: '',
                clientName: '',
                brokerName: '',
                facility: { value: '', label: '' },
                department: '',
                callerName: '',
                contactNo: '',
                ticketType: '',
                specificType: '',
                urgency: '',
                agent: { id: '', name: '' },
                additionalNotes: '',
                additionalFiles: [],
                ticketNo: '',
                status: 'Open',
                createdBy: { id: '1', name: 'Jilmer Cariaso - Static' },
                dateCreated: new Date(),
                dateUpdated: new Date(),
                dateEscalated: null
            }
        };

        this.tabList = [
            { value: 1, label: 'Summary' }, 
            { value: 2, label: 'Updates' }, 
            { value: 3, label: 'Room Accommodations' }, 
            { value: 4, label: 'Diagnoses' }, 
            { value: 5, label: 'Physicians' }, 
            { value: 6, label: 'Procedures' },  
            { value: 7, label: 'PhilHealth & Discharge Details' }, 
            { value: 8, label: 'Other Details' }, 
            { value: 9, label: 'Uploaded Documents' }
        ];

        this.facilities = [];
        this.agents = [];
        this.doctors = [];
        this.cptCodes = [];
        this.icd10Codes = [];
        this.errors = [];
    }

    componentDidMount() {

        API.get('customer-care/tickets/' + this.props.match.params['ticketId'], 
            (response) => {
                console.log(response);
                this.setState({loadedResources: this.state.loadedResources + 1});
                const ticketCategory = response.ticket_category;
                const confinement = ticketCategory.confinement_details;
                const inquiry = ticketCategory.inquiry_details;

                this.setState({ticket: {
                    ticketChannel: response.received_via,
                    transactionNo: response.transaction_no,
                    ticketSource: response.received_from,
                    memberName: response.member_name,
                    clientName: response.corporate_client,
                    brokerName: response.broker,
                    facility: { value: '', label: response.hospital_name },
                    department: response.department,
                    callerName: response.caller_name,
                    contactNo: response.contact_no,
                    ticketType: ticketCategory.ticket_type,
                    specificType: ticketCategory.specific_type,
                    urgency: ticketCategory.urgency,
                    agent: { id: '', name: ticketCategory.assign_to },
                    additionalNotes: response.additional_notes,
                    additionalFiles: [],
                    ticketNo: this.props.match.params['appNo'],
                    status: response.status,
                    createdBy: { id: '1', name: 'Jilmer Cariaso - Static' },
                    dateCreated: new Date(response.date_created),
                    dateUpdated: new Date(response.date_updated),
                    dateEscalated: new Date(response.date_escalated)
                }});

                if (confinement) {
                    const providerData = confinement.provider.split(' - ');
                    this.setState({confinement: {
                        provider: { value: providerData[0], label: providerData[1] },
                        dateAdmitted: new Date(confinement.date_admitted),
                        confinementKind: confinement.confinement_kind,
                        giftGiven: confinement.gift_given,
                        coverage: { value: '', label: confinement.coverage },
                        philhealthAmount: confinement.philhealth_amount,
                        finalBill: confinement.final_bill,
                        dischargeDate: confinement.discharge_date ? new Date(confinement.discharge_date) : null,

                        primaryDiagnosisIndex: confinement.diagnoses
                            .map((value, index) => {
                                if (value.is_primary) {
                                    return index;
                                }
                                return -1;
                            })
                            .filter(value => value != -1)[0],

                        updates: confinement.updates.map(value => {
                            return {
                                remarks: value.remarks,
                                date: new Date(value.date),
                                hospitalBill: value.hospital_bill
                            };
                        }),
                        accommodations: confinement.room_accommodation.map(value => {
                            return {
                                roomType: value.room_type,
                                days: value.no_of_days,
                                costPerDay: value.cost
                            };
                        }),
                        diagnoses: confinement.diagnoses.map(value => {
                            return {
                                diagnosis: { value: value.code, label: value.name },
                                remarks: value.remarks,
                                isFinal: value.is_final,
                                isPrimary: value.is_primary
                            };
                        }),
                        physicians: [].concat(...confinement.diagnoses.map(diagnosis => {
                            return diagnosis.doctors.map(doctor => {
                                return {
                                    physician: {
                                        value: '',
                                        label: doctor.name,
                                        params: [doctor.specialization, doctor.type]
                                    },
                                    diagnosis: { value: diagnosis.code, label: diagnosis.name },
                                    remarks: doctor.remarks,
                                    visits: doctor.no_of_visits,
                                    amount: doctor.fee
                                };
                            });
                        })),
                        procedures: [].concat(...confinement.diagnoses.map(diagnosis => {
                            return diagnosis.procedures.map(procedure => {
                                return {
                                    procedure: { value: procedure.code, label: procedure.name },
                                    diagnosis: { value: diagnosis.code, label: diagnosis.name },
                                    physician: { value: '', label: procedure.doctor },
                                    date: new Date(procedure.date_added),
                                    remarks: procedure.notes,
                                    amount: parseInt(procedure.amount)
                                };
                            });
                        })),
                        items: confinement.others.map(value => {
                            return {
                                name: value.name,
                                type: value.type,
                                amount: value.amount
                            };
                        }),
                        files: response.supporting_documents.map(value => {
                            return {
                                docName: value.name,
                                file: value.file
                            };
                        })
                    }});
                }
                
                if (inquiry) {
                    this.setState({inquiries: inquiry.inquiry_list.map(value => {
                        return {
                            inquiry: { value: '', label: value.inquiry },
                            detail: value.value,
                            specificDetail: value.specific_detail,
                            answer: value.answer,
                            remarks: value.remarks,
                            relatedTickets: value.related_tickets,
                            supportDocs: value.support_docs
                        };
                    })});
                }

                /* Get Member Details */
                API.get('customer-care/members/' + response.member_id, 
                    (response) => {
                        console.log(response);
                        this.setState({member: {
                            cardNo: response.member_card_no,
                            firstName: response.first_name,
                            middleName: response.middle_name,
                            lastName: response.last_name,
                            clientName: response.client.registered_name,
                            birthDate: new Date(response.date_of_birth),
                            gender: response.gender,
                            civilStatus: response.civil_status
                        }});

                        API.get('customer-care/members/' + response.member_card_no + '/benefits',
                            (response) => {
                                console.log(response);
                            },
                            (error) => {
                                console.log(error);
                            });
                    },
                    (error) => {
                        console.log(error);
                    });
            },
            (error) => {
                console.log(error);
            });

            API.get('facilities',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.facilities = response.map(facility => {
                    return { 
                        value: facility.facility_id,
                        label: facility.name 
                    }
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('icd10-codes?filter[limit]=1000',
            (response) => {
                console.log(response);  
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.icd10Codes = response.map(icd10Code => {
                    return { 
                        value: icd10Code.full_code,
                        label: icd10Code.abbreviated_description 
                    };
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('cpts?filter[limit]=1000',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.cptCodes = response.map(cptCode => {
                    return {
                        value: cptCode.code,
                        label: cptCode.label 
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });

        API.get('doctors',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.doctors = response.map(doctor => {
                    return {
                        value: doctor._id,
                        label: ('Dr. ' + doctor.first_name +  ' ' + doctor.last_name),
                        params: [doctor.type]
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });
    }

    getErrorList = () => {
        let errors: ErrorMessageFormat[] = [];

        const confinement = this.state.confinement;
        
        if (confinement) {

            /* Check if there's a physician assigned to each diagnosis */
            let errorDiagnosis = confinement.diagnoses.filter(diagnosis => {
                let matched = false;
                confinement.physicians.map(physician => {
                    matched = physician.diagnosis.value === diagnosis.diagnosis.value ? true : matched;
                });
                return !matched;
            });

            if (errorDiagnosis.length > 0) {
                errors.push({
                    message: 'Please assign a physician for the following diagnoses before proceding:',
                    targets: errorDiagnosis.map(diagnosis => {
                        return `${diagnosis.diagnosis.value} ${diagnosis.diagnosis.label}`
                    })
                });
                this.setState({tabSelected: 5});
            }

            
        }

        return errors;
    }

    apiSaveTicket(onSuccess: Function): void {
        if (!this.state.member) {
            return;
        }
        
        const payload = Payloads.getPutTicketPayload(
            this.state.ticket,
            this.state.member.cardNo, 
            this.state.confinement,
            this.state.inquiries
        );

        console.log(payload);
        API.put('customer-care/tickets/' + this.props.match.params['ticketId'], payload,
            (response) => {
                console.log(response);
                //Check which task case is at
                Processmaker.get('cases/'+this.props.match.params['caseId'],
                    (response) => {
                        console.log(response);
                        if (response['current_task'][0]['tas_title'] === 'Create Ticket') {
                            //Route Case to Modify/Update Ticket
                            Processmaker.put('cases/'+this.props.match.params['caseId']+'/route-case', {},
                                (response) => {
                                    console.log(response);
                                    onSuccess();
                                    // this.props.history.push('#/customer-care/tickets');
                                    // window.location.reload();
                                },
                                (error) => {
                                    console.log(error);
                                }
                            );
                        } else {
                            this.props.history.push('#/customer-care/tickets');
                            window.location.reload();
                        }
                    },
                    (error) => {
                        console.log(error);
                    }
                );
            },
            (error) => {
                console.log(error);
            });
    }

    render(): JSX.Element {
        return (
            <Grid container className='customer-care-main'>
                {this.state.loadedResources !== UpdateTicketPage.RESOURCES_TO_LOAD ? (
                    <div 
                        style={{
                            backgroundColor: '#00000055', 
                            zIndex: 100, 
                            position: 'fixed', 
                            width: '100%', 
                            height: '100%'}}>
                        <Loading height='10rem'/>
                    </div>
                    
                ) : (null)}

                <CustomModal
                    className='modal'
                    isOpen={this.state.isAssessmentModalOpen}
                    handleClose={() => this.setState({isAssessmentModalOpen: false})}
                    renderHeader={() => {
                        return <>Missing Information</>
                    }}
                    renderContent={() => {
                        return (
                            <div className='header-2'>
                                {this.errors.map(error => {
                                    return (
                                        <>
                                        <p>{error.message}</p>
                                        <p>
                                            {error.targets.map(target => {
                                                return <div>{`- ${target}`}</div>
                                            })}
                                        </p>
                                        </>
                                    );
                                })}
                            </div>
                        );
                    }}
                    renderFooter={() => {
                        return (
                            <Button 
                                variant='outlined' 
                                color='primary'
                                onClick={() => {this.setState({isAssessmentModalOpen: false})}}>
                                Okay
                            </Button>
                        );
                    }} />
                
                <MemberDataSection 
                    member={this.state.member}
                    ticket={this.state.ticket}
                    ticketStatus='Update'
                    onUpdateMemberStateChange={(_member: MemberData) => null} />
                {this.state.confinement ? (
                    this.renderInpatientContent(this.state.confinement)
                ) : this.state.inquiries ? (
                    this.renderInquiryContent(this.state.inquiries)
                ) : null}
                <UpdateTicketFooter 
                    isUpdating={false}
                    onSaveForNow={() => null} 
                    onAssign={() => null}
                    onEscalate={() => null}
                    onResolve={() => {
                        this.errors = this.getErrorList();
                        console.log(this.errors.length);
                        if (this.errors.length == 0) {
                            this.apiSaveTicket(() => {
                                this.props.history.push(
                                    '#/customer-care/resolve-ticket/' +
                                    this.props.match.params['caseId'] + '/' +
                                    this.props.match.params['appNo'] + '/' +
                                    this.props.match.params['ticketId']);
                                window.location.reload();
                            });
                        } else {
                            this.setState({isAssessmentModalOpen: true});
                        }
                    }}
                    onUpdateTicket={() => {
                        this.apiSaveTicket(() => {
                            this.props.history.push('#/customer-care/tickets');
                            window.location.reload();
                        });
                    }} />
            </Grid>
        );
    }

    renderInpatientContent(confinement: ConfinementDetails): JSX.Element {
        return (
            <div className='update-ticket'>
                <CustomTabs 
                    tabList={this.tabList}
                    selectedTab={this.state.tabSelected}
                    setSelectedTab={(index: number) => {
                        this.setState({tabSelected: index});
                    }} />
                    
                {this.state.tabSelected === 1 ? (
                    <SummarySection 
                        ticketNo={this.state.ticket.ticketNo} 
                        updates={confinement.updates}
                        accommodations={confinement.accommodations}
                        diagnoses={confinement.diagnoses}
                        physicians={confinement.physicians}
                        procedures={confinement.procedures}
                        items={confinement.items}
                        provider={confinement.provider}
                        dateAdmitted={confinement.dateAdmitted}
                        confinementKind={confinement.confinementKind}
                        coverage={confinement.coverage}
                        philhealthAmount={confinement.philhealthAmount}
                        finalBill={confinement.finalBill}
                        dischargeDate={confinement.dischargeDate}
                        setTabSelected={(value: number) => this.setState({tabSelected: value})}/>
                    

                ) : this.state.tabSelected === 2 ? (
                    <UpdateSection 
                        updates={confinement.updates}
                        refreshUpdatesState={() => {
                            this.setState({confinement: confinement});
                        }} /> 

                ) : this.state.tabSelected === 3 ? (
                    this.state.ticket ? (
                        <AccommodationsSection
                            accommodations={confinement.accommodations}
                            facilitiesList={this.facilities}
                            provider={confinement.provider}
                            dateAdmitted={confinement.dateAdmitted}
                            confinementKind={confinement.confinementKind}
                            giftGiven={confinement.giftGiven}
                            refreshAccommodationsState={() => {
                                this.setState({confinement: confinement});
                            }}
                            onUpdateTicketStateChange={(attrName: string, value: any) => {
                                this.setState(prevState => ({
                                    ...prevState,
                                    confinement: {
                                        ...confinement,
                                        [attrName]: value
                                    }
                                }));
                            }} />
                    ) : null

                ) : this.state.tabSelected === 4 ? (
                    this.state.ticket ? (
                        <DiagnosesSection 
                            diagnoses={confinement.diagnoses}
                            physicians={confinement.physicians}
                            procedures={confinement.procedures}
                            diagnosesList={this.icd10Codes}
                            primaryDiagnosisIndex={confinement.primaryDiagnosisIndex}
                            refreshDiagnosisStateChange={() => {
                                this.setState({confinement: confinement});
                            }}
                            setPrimaryDiagnosisIndex={(index: number) => {
                                // this.setState({primaryDiagnosisIndex: index});
                                this.setState(prevState => ({
                                    ...prevState,
                                    confinement: {
                                        ...confinement,
                                        primaryDiagnosisIndex: index
                                    }
                                }));
                            }} />
                    ) : null

                ) : this.state.tabSelected === 5 ? (
                    <PhysiciansSection 
                        physicians={confinement.physicians}
                        physiciansList={this.doctors}
                        diagnosesList={confinement.diagnoses}
                        refreshPhysiciansStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />

                ) : this.state.tabSelected === 6 ? (
                    <ProceduresSection
                        procedures={confinement.procedures}
                        proceduresList={this.cptCodes}
                        diagnosesList={confinement.diagnoses}
                        physiciansList={this.doctors}
                        refreshProceduresStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />

                ) : this.state.tabSelected === 7 ? (
                    <DischargeDetailsSection 
                        dateAdmitted={confinement.dateAdmitted}
                        coverage={confinement.coverage} 
                        philhealthAmount={confinement.philhealthAmount}
                        finalBill={confinement.finalBill} 
                        dischargeDate={confinement.dischargeDate}
                        onUpdateTicketStateChange={(attrName: string, value: any) => {
                            this.setState(prevState => ({
                                ...prevState,
                                confinement: {
                                    ...confinement,
                                    [attrName]: value
                                }
                            }));
                        }} />
                    
                ) : this.state.tabSelected === 8 ? (
                    <OtherDetailsSection 
                        items={confinement.items}
                        refreshItemsStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />
                    
                ) : this.state.tabSelected === 9 ? (
                    <UploadedDocumentsSection 
                        files={confinement.files}
                        refreshFilesStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />
                    
                    
                    
                ) : null}
            </div>
        );
    }

    renderInquiryContent(inquiries: InquiryData[]): JSX.Element {
        return (
            <UpdateInquirySection 
                inquiries={inquiries}
                refreshInquiryState={() => {
                    this.setState({inquiries: inquiries});
                }} />
        );
    }
}

