import * as React from 'react';
import { CRUDTable } from 'Pages/CustomerCarePage/UpdateTicketPage/Components/CRUDTable';
import { Grid, TableCell } from '@material-ui/core';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { DatePicker } from 'Pages/CustomerCarePage/Components/DatePicker';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';
import { ProcedureData, DiagnosisData } from '../UpdateTicketProps';

interface IProps {
    procedures: ProcedureData[];
    proceduresList: DropdownChoice[];
    diagnosesList: DiagnosisData[];
    physiciansList: DropdownChoice[];
    // addProcedure: (procedure: ProcedureData) => void;
    // editProcedure: (index: number, procedure: ProcedureData) => void;
    // deleteProcedure: (index: number) => void;
    refreshProceduresStateChange: () => void;
}

interface IStates {
    procedure: DropdownChoice;
    diagnosis: DropdownChoice;
    physician: DropdownChoice;
    remarks: string;
    date: Date;
    amount: number;
}

export class ProceduresSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            procedure: { value: '', label: '' },
            diagnosis: { value: '', label: '' },
            physician: { value: '', label: '' },
            remarks: '',
            date: new Date(),
            amount: 0
        }
    }

    onFormatTableHeader = (colIndex: number, value: any) => {
        return (
            colIndex === 0 ? (
                <TableCell style={{minWidth: '200px'}}>{value}</TableCell>
            ) : colIndex === 1 ? (
                <TableCell style={{minWidth: '100px'}}>{value}</TableCell>
            ) : colIndex === 2 || colIndex === 3 ? (
                <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
            ) : colIndex === 5 ? (
                <TableCell style={{textAlign: 'right', minWidth: '100px'}}>{value}</TableCell>
            ) : (
                <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
            )
        );
    }

    onFormatCRUDTable = (colIndex: number, value: any) => {
        return (
            colIndex === 0 ? (
                <div>{value.value + ' - ' + value.label}</div>
            ) : colIndex === 1 ? (
                <div>{value.value}</div>
            ) : colIndex === 2 ? (
                <div>{value.label}</div>
            ) : colIndex === 3 ? (
                <div>{GlobalFunction.toCommonDateString(value)}</div>
            ) : colIndex === 5 ? (
                <div style={{textAlign: 'right'}}>
                    {GlobalFunction.formatNumber(value, true) + ' PHP'}
                </div>
            ) : (value)
        );
    }

    render(): JSX.Element {

        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Add Procedure</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <DetailedDropdown 
                                placeholder='Procedure'
                                value={this.state.procedure}
                                choices={this.props.proceduresList}
                                onChange={(value: DropdownChoice) => {
                                    this.setState({procedure: value});
                                }}/>
                        </Grid>
                        <Grid item xs={6}>
                            <DetailedDropdown 
                                placeholder='Diagnosis'
                                value={this.state.diagnosis}
                                choices={this.props.diagnosesList.map(value => {
                                    return value.diagnosis; 
                                })}
                                onChange={(value: DropdownChoice) => {
                                    this.setState({diagnosis: value});
                                }}/>
                        </Grid>
                        <Grid item xs={6}>
                            <DetailedDropdown 
                                placeholder='Doctor'
                                detail={this.state.physician.params ? this.state.physician.params[0] : undefined}
                                value={this.state.physician}
                                choices={this.props.physiciansList}
                                onChange={(value: DropdownChoice) => {
                                    this.setState({physician: value});
                                }}/>
                        </Grid>
                        <Grid item xs={6}>
                            <InputField 
                                placeholder='Remarks'
                                value={this.state.remarks}
                                charLimit={100}
                                onChange={(value: string) => {
                                    this.setState({remarks: value});
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            <DatePicker 
                                placeholder='Date'
                                value={this.state.date}
                                onChange={(value: Date) => {
                                    this.setState({date: value});
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            <DetailedNumberField 
                                placeholder='Amount'
                                detail='PHP'
                                value={this.state.amount}
                                onChange={(value: number) => {
                                    this.setState({amount: value});
                                }}/>
                        </Grid>
                    </Grid>
                    <div style={{paddingTop: '5px'}} />
                    <TextButton 
                        label='Add procedure' 
                        icon={faPlus}
                        onClick={() => {
                            var data = this.state;
                            const tempData = {
                                procedure: data.procedure,
                                diagnosis: data.diagnosis,
                                physician: data.physician,
                                date: data.date,
                                remarks: data.remarks,
                                amount: data.amount
                            }
                            // this.props.addProcedure(tempData);
                            this.props.procedures.push(tempData);
                            this.props.refreshProceduresStateChange();
                            this.setState({
                                procedure: { value: '', label: '' },
                                diagnosis: { value: '', label: '' },
                                physician: { value: '', label: '' },
                                remarks: '',
                                date: new Date(),
                                amount: 0
                            });
                        }}/>
                </div>
                <div className='sub-section'>
                    <CRUDTable 
                        className='custom-table'
                        placeholder='PROCEDURES'
                        
                        cols={['Procedure', 'Diagnosis', 'Doctor', 'Date', 'Remarks', 'Amount']} 
                        rows={this.props.procedures}
                        onSaveEditRow={(index: number, data: any) => {
                            // this.props.editProcedure(index, data);
                            this.props.procedures[index] = data;
                            this.props.refreshProceduresStateChange();
                        }}
                        onDeleteRow={(index: number) => {
                            // this.props.deleteProcedure(index);
                            this.props.procedures.splice(index, 1);
                            this.props.refreshProceduresStateChange();
                        }}
                        onHeaderFormat={(colIndex: number, colName: string) => {
                            return this.onFormatTableHeader(colIndex, colName);
                        }}
                        onFormat={(_rowIndex: number, colIndex: number, value: any) => {
                            return this.onFormatCRUDTable(colIndex, value);
                        }}
                        editField={(colIndex: number, currentVal: any, onChange: any) => {
                            if (colIndex === 0) {
                                return (
                                    <DetailedDropdown
                                        choices={this.props.proceduresList}
                                        value={currentVal}
                                        onChange={(value: DropdownChoice) => onChange(value)}/>
                                );
                            } else if (colIndex === 1) {
                                return (
                                    <DetailedDropdown
                                        choices={this.props.diagnosesList.map(value => {
                                            return value.diagnosis; 
                                        })}
                                        value={currentVal}
                                        onChange={(value: DropdownChoice) => onChange(value)}/>
                                );
                            } else if (colIndex === 2) {
                                return (
                                    <DetailedDropdown
                                        detail={currentVal.params[0]}
                                        choices={this.props.physiciansList}
                                        value={currentVal}
                                        onChange={(value: DropdownChoice) => onChange(value)}/>
                                ); 
                            } else if (colIndex === 3) {
                                return (
                                    <DatePicker
                                        value={currentVal}
                                        onChange={(value: Date) => onChange(value)}/>
                                );
                            } else if (colIndex === 4) {
                                return (
                                    <InputField
                                        value={currentVal}
                                        charLimit={100}
                                        onChange={(value: string) => onChange(value)}/>
                                );
                            
                            } else {
                                return (
                                    <DetailedNumberField
                                        detail='PHP'
                                        value={currentVal}
                                        onChange={(value: number) => onChange(value)}/>
                                );
                            }
                        }} />
                </div>
                <div className='section' />
            </div>
        );
    }
}