import { TicketData, ConfinementDetails, InquiryData, AvailmentDetails, LOAData, ConsultationDetails } from "./Models";
import { ConfinementDetails as UpdateInpatientDetails } from "./UpdateTicketPage/UpdateTicketPage";
import { ConfinementDetails as ResolveInpatientDetails } from "./ResolveTicketPage/ResolveTicketPage";


export class Payloads {

    static getPostTicketPayload(ticket: TicketData,
                                confinement: ConfinementDetails, 
                                availment: AvailmentDetails, 
                                inquiries: InquiryData[],
                                consultation: ConsultationDetails,
                                memberCardNo: string): any  {
        return {
            transaction_no: ticket.transactionNo,
            received_via: ticket.ticketChannel,
            received_from: ticket.ticketSource,

            member_name: (ticket.ticketSource === 'Member' ? ticket.memberName : undefined),
            corporate_client: (ticket.ticketSource === 'Corporate Client' ? ticket.clientName : undefined),
            broker: (ticket.ticketSource === 'Broker' ? ticket.brokerName : undefined),
            hospital_name: (ticket.ticketSource === 'Clinic/Hospital' ? (ticket.facility.value + ' - ' + ticket.facility.label) : undefined),
            department: (ticket.ticketSource === 'Clinic/Hospital' ? ticket.department : undefined),

            caller_name: ticket.callerName,
            contact_no: ticket.contactNo,
            additional_notes: ticket.additionalNotes,
            supporting_documents: [],

            ticket_category: {
                ticket_type: ticket.ticketType,
                specific_type: ticket.specificType,
                urgency: ticket.urgency,
                assign_to: ticket.agent.name,

                availment_details: (
                    ticket.ticketType === 'Availment' &&
                    ticket.specificType === 'Outpatient Procedure' ? {
                    provider: availment.provider.value + ' - ' + availment.provider.label,
                    availments_list: availment.diagnoses.map(availment => {
                        return {
                            code: availment.icd10Code.value,
                            name: availment.icd10Code.label,
                            remarks: '',
                            is_primary: false,
                            is_final: false,
                            
                            doctors: [
                                {
                                    id: '',
                                    name: availment.doctors[0].name,
                                    specialization: (confinement.doctor.params ? confinement.doctor.params[0] : null),
                                    fee: 0,
                                    no_of_visits: '',
                                    remarks: ''
                                }
                            ],

                            procedures: availment.procedures
                                .filter(procedure => {
                                    return procedure.cptCode.value;
                                })
                                .map(procedure => {
                                    return {
                                        code: procedure.cptCode.value,
                                        name: procedure.cptCode.label,
                                        amount: procedure.amount.toString(),
                                        doctor: '',
                                        phic: procedure.isPHIC,
                                        notes: '',
                                        date_added: new Date(),
                                        assessment_type: procedure.assessmentType,
                                        assessment_remarks: procedure.assessmentRemarks
                                    }
                                }),
                            
                                assessment_type: availment.assessmentType,
                                assessment_remarks: availment.assessmentRemarks
                        }
                    })
                } : undefined),

                confinement_details: (
                    ticket.ticketType === 'Availment' &&
                    ticket.specificType === 'Inpatient' ? {
                    provider: confinement.provider.value + ' - ' + confinement.provider.label,
                    date_admitted: confinement.dateAdmitted,
                    confinement_kind: confinement.confinementKind,
                    gift_given: false,
                    updates: [],
                    room_accommodation: [{
                        room_type: confinement.roomType,
                        no_of_days: '1',
                        cost: confinement.costPerDay
                    }],
                    diagnoses: [
                        {
                            code: confinement.initialDiagnosis.value,
                            name: confinement.initialDiagnosis.label,
                            remarks: '',
                            is_primary: false,
                            is_final: false,
                            
                            doctors: [
                                {
                                    id: '',
                                    name: confinement.doctor.label,
                                    specialization: (confinement.doctor.params ? confinement.doctor.params[0] : null),
                                    fee: confinement.amount,
                                    no_of_visits: '0',
                                    remarks: '',
                                    type: 'Admitting'
                                }
                            ],

                            procedures: []
                        }
                    ],
                    others: [],
                    coverage: 'Filed',
                    final_bill: 0,
                    philhealth_amount: 0,
                    discharge_date: null
                } : undefined),

                consultation_details: (
                    ticket.specificType === 'Consultation' ? {
                    provider: `${consultation.provider.value} - ${consultation.provider.label}`,
                    diagnosis: `${consultation.diagnosis.value} - ${consultation.diagnosis.label}`,
                    doctor: `${consultation.doctor.value} - ${consultation.doctor.label}`,
                    notes: consultation.notes,
                    assessment_type: consultation.assessmentType,
                    assessment_remarks: consultation.assessmentRemarks
                } : undefined),

                inquiry_details: (
                    ticket.ticketType === 'Inquiry' ? {
                    inquiry_list: inquiries.map(inquiry => {
                        return {
                            inquiry: inquiry.inquiry.label,
                            value: inquiry.detail,
                            specific_detail: inquiry.specificDetail,
                            answer: inquiry.answer,
                            remarks: inquiry.remarks, 
                            related_tickets: inquiry.relatedTickets, 
                            support_docs: []
                        }
                    })
                } : undefined)
            },
            ticket_no: '',
            status: 'Open',
            date_created: new Date(),
            date_updated: new Date(),
            date_escalated: null,
            created_by: {
                _id: ticket.createdBy.id,
                name: ticket.createdBy.name
            },
            member_id: memberCardNo,
            client_id: '',
            
            loa: []
        };
    }










    static getPutTicketPayload(ticket: TicketData, 
                               memberCardNo: string,
                               confinement?: UpdateInpatientDetails,
                               inquiries?: InquiryData[]): any {
        return {
            transaction_no: ticket.transactionNo,
            received_via: ticket.ticketChannel,
            received_from: ticket.ticketSource,

            member_name: ticket.memberName,
            corporate_client: ticket.clientName,
            broker: ticket.brokerName,
            hospital_name: ticket.facility.label,
            department: ticket.department,

            caller_name: ticket.callerName,
            contact_no: ticket.contactNo,
            additional_notes: ticket.additionalNotes,
            supporting_documents: (confinement ? [
                ...confinement.files.map(file => {
                    return {
                        name: file.docName,
                        type: 'null',
                        url: 'null',
                        file: file.file
                    }
                })
             ] : []),

            ticket_category: {
                ticket_type: ticket.ticketType,
                specific_type: ticket.specificType,
                urgency: ticket.urgency,
                assign_to: ticket.agent.name,

                confinement_details: (confinement ? {
                    provider: `${confinement.provider.value} - ${confinement.provider.label}`,
                    date_admitted: confinement.dateAdmitted,
                    confinement_kind: confinement.confinementKind,
                    gift_given: confinement.giftGiven,
                    updates: confinement.updates.map(update => {
                        return {
                            remarks: update.remarks,
                            date: update.date,
                            hospital_bill: update.hospitalBill.toString()
                        }
                    }),
                    room_accommodation: confinement.accommodations.map(accommodation => {
                        return {
                            room_type: accommodation.roomType,
                            no_of_days: accommodation.days.toString(),
                            cost: accommodation.costPerDay
                        }
                    }),
                    diagnoses: confinement.diagnoses.map((diagnosis, index) => {
                        return {
                            code: diagnosis.diagnosis.value,
                            name: diagnosis.diagnosis.label,
                            remarks: diagnosis.remarks,
                            is_primary: (confinement.primaryDiagnosisIndex === index),
                            is_final: diagnosis.isFinal,
                            doctors: confinement.physicians
                                .filter(physician => physician.diagnosis.value === diagnosis.diagnosis.value)
                                .map(physician => {
                                    return {
                                        id: '',
                                        name: physician.physician.label,
                                        specialization: physician.physician.params ? physician.physician.params[0] : '',
                                        fee: physician.amount,
                                        no_of_visits: physician.visits.toString(),
                                        remarks: physician.remarks,
                                        type: physician.physician.params ? physician.physician.params[1] : ''
                                    }
                                }),
                            procedures: confinement.procedures
                                .filter(procedure => procedure.diagnosis.value === diagnosis.diagnosis.value)
                                .map(procedure => {
                                    return {
                                        code: procedure.procedure.value + '', // Convert to string
                                        name: procedure.procedure.label,
                                        amount: procedure.amount.toString(),
                                        doctor: procedure.physician.label,
                                        phic: false,
                                        notes: procedure.remarks,
                                        date_added: procedure.date,
                                        assessment_type: 'Pending',
                                        assessment_remarks: ''
                                    }
                                }),
                            assessment_type: 'Pending',
                            assessment_remarks: ''
                        }
                    }),
                    others: confinement.items.map(item => {
                        return {
                            amount: item.amount,
                            name: item.name,
                            type: item.type
                        }
                    }),
                    coverage: confinement.coverage.label,
                    philhealth_amount: confinement.philhealthAmount,
                    final_bill: confinement.finalBill,
                    discharge_date: confinement.dischargeDate
                } : undefined),

                inquiry_details: (inquiries ? {
                    inquiry_list: inquiries.map(inquiry => {
                        return {
                            inquiry: inquiry.inquiry.label,
                            value: inquiry.detail,
                            specific_detail: inquiry.specificDetail,
                            answer: inquiry.answer,
                            remarks: inquiry.remarks, 
                            related_tickets: inquiry.relatedTickets, 
                            support_docs: []
                        }
                    })
                } : undefined)
            },
            ticket_no: ticket.ticketNo,
            status: ticket.status,
            date_created: ticket.dateCreated,
            date_updated: ticket.dateUpdated,
            date_escalated: null,
            created_by: {
                _id: ticket.createdBy.id,
                name: ticket.createdBy.name
            },
            member_id: memberCardNo,
            client_id: '',

            loa: []
        };
    }










    static getPutResolveTicketPayload(ticket: TicketData, 
                                      memberCardNo: string,
                                      loas: LOAData[],
                                      availment?: AvailmentDetails,
                                      confinement?: ResolveInpatientDetails,
                                      consultation?: ConsultationDetails): any {
        return {
            transaction_no: ticket.transactionNo,
            received_via: ticket.ticketChannel,
            received_from: ticket.ticketSource,

            member_name: ticket.memberName,
            corporate_client: ticket.clientName,
            broker: ticket.brokerName,
            hospital_name: ticket.facility.label,
            department: ticket.department,

            caller_name: ticket.callerName,
            contact_no: ticket.contactNo,
            additional_notes: ticket.additionalNotes,
            supporting_documents: [],

            ticket_category: {
                ticket_type: ticket.ticketType,
                specific_type: ticket.specificType,
                urgency: ticket.urgency,
                assign_to: ticket.agent.name,

                availment_details: (availment ? {
                    provider: availment.provider.value + ' - ' + availment.provider.label,
                    availments_list: availment.diagnoses.map(availment => {
                        return {
                            code: availment.icd10Code.value,
                            name: availment.icd10Code.label,
                            remarks: '',
                            is_primary: false,
                            is_final: false,
                            
                            doctors: [
                                {
                                    id: '',
                                    name: availment.doctors[0].name,
                                    specialization: '',
                                    fee: 0,
                                    no_of_visits: '',
                                    remarks: ''
                                }
                            ],

                            procedures: availment.procedures
                                .filter(procedure => {
                                    return procedure.cptCode.value;
                                })
                                .map(procedure => {
                                    return {
                                        code: procedure.cptCode.value,
                                        name: procedure.cptCode.label,
                                        amount: procedure.amount.toString(),
                                        doctor: '',
                                        phic: procedure.isPHIC,
                                        notes: '',
                                        date_added: new Date(),
                                        assessment_type: procedure.assessmentType,
                                        assessment_remarks: procedure.assessmentRemarks
                                    }
                                })
                        }
                    })
                } : undefined),

                confinement_details: (confinement ? {
                    provider: confinement.provider.value + ' - ' + confinement.provider.label,
                    date_admitted: confinement.dateAdmitted,
                    confinement_kind: confinement.confinementKind,
                    gift_given: confinement.giftGiven,
                    updates: confinement.updates,
                    room_accommodation: confinement.accommodations.map(accommodation => {
                        return {
                            room_type: accommodation.roomType,
                            no_of_days: accommodation.days.toString(),
                            cost: accommodation.costPerDay
                        }
                    }),
                    diagnoses: confinement.diagnoses.map(diagnosis => {
                        return {
                            code: diagnosis.icd10Code.value,
                            name: diagnosis.icd10Code.label,
                            remarks: diagnosis.remarks,
                            is_primary: diagnosis.isPrimary,
                            is_final: diagnosis.isFinal,
                            doctors: diagnosis.doctors.map(doctor => {
                                return {
                                    id: doctor.id,
                                    name: doctor.name,
                                    specialization: doctor.specialization,
                                    fee: doctor.fee,
                                    no_of_visits: doctor.noOfVisits.toString(),
                                    remarks: doctor.remarks,
                                    type: doctor.type
                                }
                            }),
                            procedures: diagnosis.procedures.map(procedure => {
                                return {
                                    code: procedure.cptCode.value + '', // Convert to string
                                    name: procedure.cptCode.label,
                                    amount: procedure.amount.toString(),
                                    doctor: procedure.doctor,
                                    phic: procedure.isPHIC,
                                    notes: procedure.notes,
                                    date_added: procedure.dateAdded,
                                    assessment_type: procedure.assessmentType,
                                    assessment_remarks: procedure.assessmentRemarks
                                }
                            }),
                            assessment_type: diagnosis.assessmentType,
                            assessment_remarks: diagnosis.assessmentRemarks
                        }
                    }),
                    others: confinement.others,
                    coverage: confinement.coverage.label,
                    philhealth_amount: confinement.philhealthAmount,
                    final_bill: confinement.finalBill,
                    discharge_date: confinement.dischargeDate
                } : undefined),

                consultation_details: (consultation ? {
                    provider: `${consultation.provider.value} - ${consultation.provider.label}`,
                    diagnosis: `${consultation.diagnosis.value} - ${consultation.diagnosis.label}`,
                    doctor: `${consultation.doctor.value} - ${consultation.doctor.label}`,
                    notes: consultation.notes,
                    assessment_type: consultation.assessmentType,
                    assessment_remarks: consultation.assessmentRemarks
                } : undefined)
            },
            ticket_no: ticket.ticketNo,
            status: 'Resolved',
            date_created: ticket.dateCreated,
            date_updated: ticket.dateUpdated,
            date_escalated: null,
            created_by: {
                _id: ticket.createdBy.id,
                name: ticket.createdBy.name
            },
            member_id: memberCardNo,
            client_id: '',
            loa: loas.map(loa => {
                return {
                    loa_no: loa.loaNo,
                    amount: loa.amount,
                    date: loa.date,
                    notes: loa.notes
                }
            })
        };
    }
}