



export class GlobalFunction {

    static formatNumber(num: number, isDecimal: boolean = false) {
        let value = '';
        if (isDecimal) {
            value = num.toFixed(2);
        } else {
            value = num.toString();
        }
        return value.replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
    }


    static getIntials(text: string) {
        var initials = '';
        var wordArray = text.split(' ');
        for (var i = 0; i < wordArray.length; i++) {
            initials += wordArray[i].substring(0,1).toUpperCase() + '.';
        }
        return initials;
    }

    static toCommonDateString(date: Date) {
        if (date) {
            const monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            return (monthNames[monthIndex] + " " + day + ", " + year);
        }

        return '';
    }

    static toShortDateString(date: Date) {
        if (date) {
            const monthNames = [
                "Jan", "Feb", "Mar", "Apr", "May", "Jun", 
                "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            return (monthNames[monthIndex] + " " + day + ", " + year);
        }

        return '';
    }

    static toDateAndTime(date: Date) {
        if (date) {
            const monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            const hours = date.getHours() - (date.getHours() > 12 ? 12 : 0);
            const minutes = date.getMinutes();
            const seconds = date.getSeconds();
            const meridian = (date.getHours() > 12 ? "PM" : "AM");
            return (
                monthNames[monthIndex] + " " + 
                day + ", " + 
                year + " - " + 
                hours + ":" + 
                minutes + ":" + 
                seconds + " " + 
                meridian);
        }

        return '';
    }

    static getAge(birthDate: any) {
        if (birthDate) {
            var ageInMillis = Date.now() - birthDate;
            return Math.abs(new Date(ageInMillis).getUTCFullYear() - 1970);
        }

        return null;
    }

    static generateTicketNumber(ticketType: string, specificType: string, ticketNo: string) {
        const ticketTypes = {
            "Availment": "AVL",
            "Inquiry": "INQ"
        };
        const specificTypes = {
            "Inpatient": "INP",
            "Outpatient Procedure": "OP",
            "Emergency Room": "ER",
            "Maternity": "MAT",
            "Consultation": "CON"
        }
        // return (
        //     ticketTypes[ticketType] + 
        //     specificTypes[specificType] + 
        //     Math.random().toString(36).substr(2, 6)
        // );
        const tempString = (ticketType === 'Inquiry' ? '' : `${specificTypes[specificType]}-`);
        return `${ticketTypes[ticketType]}-${tempString}${ticketNo}`;
    }
}