import * as React from 'react';
import { Grid, Radio, TableCell } from '@material-ui/core';
import { PhysicianData, DiagnosisData } from '../UpdateTicketProps';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { CRUDTable } from 'Pages/CustomerCarePage/UpdateTicketPage/Components/CRUDTable';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';
// import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';

interface IProps {
    physicians: PhysicianData[];
    physiciansList: DropdownChoice[];
    diagnosesList: DiagnosisData[];
    // addPhysician: (physician: PhysicianData) => void;
    // editPhysician: (index: number, physician: PhysicianData) => void;
    // deletePhysician: (index: number) => void;
    refreshPhysiciansStateChange: () => void;
}

interface IStates {
    physician: DropdownChoice;
    diagnosis: DropdownChoice;
    type: string;
    remarks: string;
    visits: number;
    amount: number;
}

export class PhysiciansSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            physician: { value: '', label: '' },
            diagnosis: { value: '', label: '' },
            type: '',
            remarks: '',
            visits: 0,
            amount: 0
        }
    }

    onFormatTableHeader = (colIndex: number, value: any) => {
        return (
            colIndex === 0 ? (
                <TableCell style={{minWidth: '250px'}}>{value}</TableCell>
            ) : colIndex === 1 ? (
                <TableCell style={{minWidth: '100px'}}>{value}</TableCell>
            ) : colIndex === 3 || colIndex === 4 ? (
                <TableCell style={{minWidth: '150px', textAlign: 'right'}}>{value}</TableCell>
            ) : (
                <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
            )
        );
    }

    onFormatCRUDTable = (colIndex: number, value: any) => {
        return (
            colIndex === 0 ? (
                <div>
                    <div>{value.params[0] + ' - ' + value.label}</div>
                    <div className='light-text'>{value.params[1] + ' Physician'}</div>
                </div>
            ) : colIndex === 1 ? (
                <div>{value.value}</div>
            ) : colIndex === 3 || colIndex === 4 ? (
                <div style={{textAlign: 'right'}}>
                    {colIndex === 4 ? GlobalFunction.formatNumber(value, true) + ' PHP' : value}
                </div>
            ) : (value)
        );
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Add Physician</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <DetailedDropdown 
                                placeholder='Doctor'
                                detail={this.state.physician.params ? this.state.physician.params[0] : ''}
                                value={this.state.physician}
                                choices={this.props.physiciansList}
                                onChange={(value: DropdownChoice) => {
                                    this.setState({physician: value});
                                }}/>
                        </Grid>
                        <Grid item xs={6}>
                            <DetailedDropdown 
                                placeholder='Diagnosis'
                                value={this.state.diagnosis}
                                choices={this.props.diagnosesList.map(value => {
                                    return value.diagnosis; 
                                })}
                                onChange={(value: DropdownChoice) => {
                                    this.setState({diagnosis: value});
                                }}/>
                        </Grid>
                        <Grid item xs={6}>
                            <InputField 
                                placeholder='Remarks'
                                value={this.state.remarks}
                                charLimit={100}
                                onChange={(value: string) => {
                                    this.setState({remarks: value});
                                }}/>
                        </Grid>
                        <Grid item xs={1}>
                            <NumberField
                                placeholder='Visits'
                                value={this.state.visits}
                                onChange={(value: number) => {
                                    this.setState({visits: value});
                                }}/>
                        </Grid>
                        <Grid item xs={2}>
                            <DetailedNumberField
                                placeholder='Amount'
                                detail='PHP'
                                value={this.state.amount}
                                onChange={(value: number) => {
                                    this.setState({amount: value});
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            {/* Checkbox here */}
                            <div>
                            <Grid container style={{paddingTop: '1rem'}}>
                                <Grid item xs={6}>
                                    <Radio 
                                        className='check-box'
                                        style={{height: '100%', width: 0}}
                                        checked={this.state.type === 'Admitting'}
                                        value={'Admitting'}
                                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                            this.setState({type: event.target.value});
                                        }} /> Admitting
                                </Grid>
                                <Grid item xs={6}>
                                    <Radio 
                                        className='check-box'
                                        style={{height: '100%', width: 0}}
                                        checked={this.state.type === 'Attending'}
                                        value={'Attending'}
                                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                            this.setState({type: event.target.value});
                                        }} /> Attending
                                </Grid>
                            </Grid>
                            </div>
                        </Grid>
                    </Grid>
                    <div style={{paddingTop: '5px'}} />
                    <TextButton 
                        label='Add physician' 
                        icon={faPlus}
                        onClick={() => {
                            var data = this.state;
                            const tempData = {
                                physician: {
                                    value: data.physician.value,
                                    label: data.physician.label,
                                    params: [data.physician.params ? data.physician.params[0] : '', data.type]
                                },
                                diagnosis: data.diagnosis,
                                remarks: data.remarks,
                                visits: data.visits,
                                amount: data.amount
                            };
                            this.props.physicians.push(tempData);
                            this.props.refreshPhysiciansStateChange();
                            this.setState({
                                physician: { value: '', label: '' },
                                diagnosis: { value: '', label: '' },
                                type: '',
                                remarks: '',
                                visits: 0,
                                amount: 0
                            });
                        }}/>
                </div>
                <div className='sub-section'>
                    <CRUDTable 
                        className='custom-table'
                        placeholder='PHYSICIANS'
                        cols={['Specialization - Doctor Name', 'Diagnosis', 'Remarks', 'No. of Visits', 'Amount']} 
                        rows={this.props.physicians}
                        onSaveEditRow={(index: number, data: any) => {
                            // this.props.editPhysician(index, data);
                            this.props.physicians[index] = data;
                            this.props.refreshPhysiciansStateChange();
                        }}
                        onDeleteRow={(index: number) => {
                            // this.props.deletePhysician(index);
                            this.props.physicians.splice(index, 1);
                            this.props.refreshPhysiciansStateChange();
                        }}
                        onHeaderFormat={(colIndex: number, colName: string) => {
                            return this.onFormatTableHeader(colIndex, colName);
                        }} 
                        onFormat={(_rowIndex: number, colIndex: number, value: any) => {
                            return this.onFormatCRUDTable(colIndex, value);
                        }}
                        editField={(colIndex: number, currentVal: any, onChange: any) => {
                            if (colIndex === 0) {
                                return (
                                    <DetailedDropdown
                                        detail={currentVal.params[0]}
                                        choices={this.props.physiciansList}
                                        value={currentVal}
                                        onChange={(value: DropdownChoice) => onChange({
                                            value: value.value,
                                            label: value.label,
                                            params: [value.params ? value.params[0] : '', currentVal.params[1]]
                                        })}/>
                                );
                            } else if (colIndex === 1) {
                                return (
                                <div>{currentVal.value}</div>
                                    // <DetailedDropdown
                                    //     choices={this.props.diagnosesList.map(value => {
                                    //         return value.diagnosis; 
                                    //     })}
                                    //     value={currentVal}
                                    //     onChange={(value: DropdownChoice) => onChange(value)}/>
                                );
                            } else if (colIndex === 2) {
                                return (
                                    <InputField
                                        value={currentVal}
                                        onChange={(value: string) => onChange(value)}/>
                                );
                                
                            } else if (colIndex === 3) {
                                return (
                                    <NumberField
                                        value={currentVal}
                                        onChange={(value: number) => onChange(value)}/>
                                );
                            
                            } else {
                                return (
                                    <DetailedNumberField
                                        detail='PHP'
                                        value={currentVal}
                                        onChange={(value: number) => onChange(value)}/>
                                );
                            }
                        }} />
                </div>
                <div className='section' />
            </div>
        );
    }
}