import * as React from 'react';
import './component-style.scss';
import { GlobalFunction } from '../GlobalFunction';

interface IProps {
    placeholder?: string;
    value: number;
    detail: string;
    onChange: (value: number) => void;

    isFloat?: boolean;
}

interface IStates {
    isEditing: boolean;
}

export class DetailedNumberField extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            isEditing: false
        }
    }

    shouldComponentUpdate(props: IProps, state: IStates) {
        return (
            (this.props.placeholder !== props.placeholder) ||
            (this.props.value !== props.value) ||
            (this.props.detail !== props.detail) ||
            (this.state.isEditing !== state.isEditing)
        );
    }

    render(): JSX.Element {
        
        return (
            <div className='detailed-input-field'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                <div className='input-field'>
                    {this.state.isEditing ? (
                        <input 
                            className='text-field'
                            type='number'
                            value={this.props.value ? this.props.value : NaN}
                            onBlur={() => this.setState({isEditing: !this.state.isEditing})}
                            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                                const value = this.props.isFloat ? 
                                    (parseFloat(event.target.value)) : 
                                    (parseInt(event.target.value));
                                this.props.onChange(value);
                            }} />
                    ) : (
                        <input 
                            className='text-field'
                            readOnly
                            value={
                                this.props.value && this.props.value !== 0 ? 
                                    GlobalFunction.formatNumber(this.props.value) 
                                : ''}
                            onFocus={() => this.setState({isEditing: !this.state.isEditing})} />
                    )}
                    <div className='detail'>{this.props.detail}</div>
                </div>
            </div>
        );
    }
}